import { mapGetters } from 'vuex';
import { generateUrl } from '@nextcloud/router';
import FetchFacesMixin from '../../mixins/FetchFacesMixin.js';
import FaceCoverMixin from '../../mixins/FaceCoverMixin.js';
export default {
  name: 'FaceCover',
  mixins: [FetchFacesMixin, FaceCoverMixin],
  props: {
    baseName: {
      type: String,
      required: true
    },
    small: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      observer: null
    };
  },
  computed: {
    ...mapGetters(['files', 'faces', 'facesFiles']),
    /**
     * @return {Face}
     */
    face() {
      return this.faces[this.baseName];
    },
    /**
     * @return {string}
     */
    coverUrl() {
      if (!this.cover) {
        return '';
      }
      return generateUrl(`/apps/photos/api/v1/preview/${this.cover.fileid}?x=${512}&y=${512}`);
    },
    cover() {
      return this.getFaceCover(this.face.basename);
    },
    coverDimensions() {
      if (!this.cover) return {};
      return this.getCoverStyle(this.face.basename);
    }
  }
};